import React from "react";
import {WindBarb} from './windbarbs'
import { WeatherBlob } from "./weatherBlob";

export default class WeatherLayer extends React.Component {
    render() {
        return (
            <div>
                <WindBarb parent='#weather-layer' speed={0} angle={90} x={5} z={3} width={.5} height={.5}/>
                <WindBarb parent='#weather-layer' speed={50} angle={105} x={1.32} z={3} width={.5} height={.5}/>
                <WindBarb parent='#weather-layer' speed={50} angle={105} x={1} z={3} width={.5} height={.5}/>
                <WindBarb parent='#weather-layer' speed={50} angle={105} x={1} z={3.3} width={.5} height={.5}/>
                <WindBarb parent='#weather-layer' speed={50} angle={115} x={1.32} z={2} width={.5} height={.5}/>
                <WindBarb parent='#weather-layer' speed={50} angle={115} x={1} z={2} width={.5} height={.5}/>
                <WindBarb parent='#weather-layer' speed={50} angle={115} x={1} z={2.3} width={.5} height={.5}/>
                <WindBarb parent='#weather-layer' speed={50} angle={125} x={1.35} z={1.5} width={.5} height={.5}/>
                <WindBarb parent='#weather-layer' speed={50} angle={125} x={.7} z={1.5} width={.5} height={.5}/>
                <WindBarb parent='#weather-layer' speed={50} angle={125} x={.8} z={1.8} width={.5} height={.5}/>
                <WindBarb parent='#weather-layer' speed={50} angle={145} x={2.35} z={.5} width={.5} height={.5}/>
                <WindBarb parent='#weather-layer' speed={50} angle={145} x={1.7} z={.5} width={.5} height={.5}/>
                <WindBarb parent='#weather-layer' speed={50} angle={145} x={1.8} z={.8} width={.5} height={.5}/>
                <WeatherBlob parent='#weather-layer' startX={1} startY={1} startZ={1} endX={3} endY={3} endZ={3} width={5} height={5}/>
            </div>
        );
    }
}